<template>
      <div class="modal-mask">
        <div class="modal-wrapper" style="display: block !important;">
          <div class="modal-container" style="margin-top: 100px !important;">
            <button class="btn btn-primary btn-circle btn-circle-sm m-1 pull-right" @click="$emit('closeModal')"><i class="fa fa-times"></i></button>
            <div class="modal-header">
              <h3 slot="header" :style="{marginTop: 0, color: chooseColor}">{{headerText}}</h3>
            </div>

            <div class="modal-body">
              <slot name="body">
                <input type="password" class="form-control" ref="pass" v-model="sendDatas.password" placeholder="password">
              </slot>
            </div>

             <div class="modal-footer">
                 <button class="modal-default-button btn btn-primary" @click="confirmPass()">
                    OK
                  </button>
              </div>
           </div>
         </div>
        </div>
</template>

<script>
import Axios from 'axios'
export default{
  props: ['no'],
  data(){
    return{
      headerText : '비밀번호를 입력해주세요.',
      sendDatas: {
        password : null,
        boardNo : this.no,
      },
      flag : true
    }
  },
  computed:{
    chooseColor(){
      return this.flag ? '#42b983' : 'red';
    }
  },
  methods:{
    confirmPass(){
      if(this.sendDatas.password){
        // pass check api call
        Axios.post('/api/passConfirm', this.sendDatas).then(res => {

          if(res.data.result === 'OK'){
            this.$emit('close');
          } else if (res.data.result === 'FAIL'){
            this.flag = false;
            this.headerText = res.data.message;
          } else {
            alert(res.data.message);
            location.href="/1"
          }

        }).catch(error => {
            alert(error.toString())
        })

      }else{
        this.headerText = '비밀번호를 입력해주세요!';
        this.flag = false;
        this.$refs.pass.focus();
      }
    }
  }
}
</script>

<style>
  .modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }

  .modal-wrapper {
    display: table-cell;
    vertical-align: middle;
  }

  .modal-container {
    width: 300px;
    margin: 0px auto;
    padding: 20px 30px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
  }
  margin-top: 0;
  color: #42b983;

  .modal-body {
    margin: 20px 0;
  }

  .modal-default-button {
    float: right;
  }

  /*
   * The following styles are auto-applied to elements with
   * transition="modal" when their visibility is toggled
   * by Vue.js.
   *
   * You can easily play with the modal transition by editing
   * these styles.
   */

  .modal-enter {
    opacity: 0;
  }

  .modal-leave-active {
    opacity: 0;
  }

  .modal-enter .modal-container,
  .modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

</style>



